import React from "react"
import './App.css';

class App extends React.Component{
	constructor(){
		super()
		this.state = {
			name:'',
			list:[]
		}

		this.handleChange = this.handleChange.bind(this)
		this.addItem = this.addItem.bind(this)
		this.handleList = this.handleList.bind(this)
		this.onRemoveItem = this.onRemoveItem.bind(this)
	}

	addItem() {
		if (this.state.name) {
			 this.setState(state => {
				  const list = [...state.list, state.name];
				  return {
						list,
						name: ''
				  };
			 });
		}
  };
	
	onRemoveItem(i) {
		this.setState(state => {
			const list = state.list.filter((item, j) => i !== j);
			return {
				list,
			};
		});
	};	


  handleList() {
	return this.state.list.map((item,index)=>
		 <li key = {item}><p>{item}</p>
			  <button className = 'btn' onClick={()=>this.onRemoveItem(index)}>delete</button>
		 </li>
	)
}

	handleChange(e) {
		this.setState({
			 name: e.target.value,
		})
  }

	render(){
		return(
			<div className = 'todo'>
				<h1 className = 'title'>ToDo List</h1>
				<input
				placeholder = '...write'
				type = 'text'
				onChange = {this.handleChange}
				value = {this.state.name}
				>
				</input>
				<button className = 'btn' onClick = {this.addItem}>To Do</button>
				<ul> {this.handleList()} </ul>
			</div>
		)
	}
}

export default App;